<template>
    <section id="splash">
        <div class="container">
            <div class="logo-1" :class="{ 'is-load': isLoad }">
                <img :src="logo_1" alt="Logo_1" />
            </div>
            <div class="logo-2" :class="{ 'is-load': isLoad }">
                <div class="row">
                    <!-- <img class="logo-2-img" :class="{ 'is-load': isLoad }" :src="logo_2" alt="Logo_2" /> -->
                    <Logo :class="{ 'is-load': isLoad }" />
                    <img class="logo-mute-img" :class="{ 'is-load': isLoad }" :src="logo_1" alt="Logo_1" />
                    <p class="tagline" :class="{ 'is-load': isLoad }">나만의 고요한 가상의 공간</p>
                    <form
                        action="#"
                        method="post"
                        name="login-form"
                        class="row login-form"
                        :class="{ 'is-load': isLoad }"
                    >
                        <div class="login-form-field">
                            <label class="login-form-label" for="login-id">
                                <span class="hidden">이메일</span>
                                <img :src="mail" alt="이메일"
                            /></label>
                            <input
                                class="input"
                                id="login-id"
                                name="id"
                                type="text"
                                placeholder="이메일을 입력하세요."
                            />
                        </div>
                        <div class="login-form-field">
                            <label class="login-form-label" for="login-id">
                                <span class="hidden">비밀번호</span>
                                <img :src="lock" alt="비밀번호"
                            /></label>
                            <input
                                class="input"
                                id="login-pass"
                                name="pass"
                                type="password"
                                placeholder="비밀번호를 입력하세요."
                            />
                        </div>
                        <div class="auth-links">
                            <a href="#">이메일찾기</a>
                            <a href="#">비밀번호찾기</a>
                        </div>
                        <button type="submit" class="login-btn">로그인</button>
                        <button type="submit" class="signup-btn">회원가입</button>
                    </form>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import '@/assets/styles/page/splash.css';
import logo1 from '@/assets/images/common/logo.png';
import logo2 from '@/assets/images/common/logo_2.png';
import eyeIcon from '@/assets/images/icon/Eye.png';
import lockIcon from '@/assets/images/icon/Lock.png';
import mailIcon from '@/assets/images/icon/Mail.png';
import Logo from '@/components/Logo.vue';

export default {
    name: 'SplashView',
    components: {
        Logo
    },
    data() {
        return {
            isLoad: false,
            logo_1: logo1,
            logo_2: logo2,
            eye: eyeIcon,
            lock: lockIcon,
            mail: mailIcon
        };
    },
    mounted() {
        requestAnimationFrame(() => {
            this.isLoad = true;
        });
    }
};
</script>

